 .newUser{
     flex:4;
     padding: 20px;
 }

 .newUserForm{
     display: flex;
     flex-wrap: wrap;
 }

 .newUserItem{
     width: 400px;
     display: flex;
     flex-direction: column;margin-top: 10px;
     margin-right: 20px;
 }

 .newUserItem>label{
     margin-bottom: 10px;
     font-size: 14px;
     font-weight: 600;
     color: rgb(155, 151, 151);
 }

 .newUserItem>input{
     height: 20px;
     padding: 10px;
     border: 1px solid gray;
     border-radius:5px;
 }

 .newUserGender>input{
    margin-top: 14px;
 }

 .newUserGender>label{
     margin: 10px;
     font-size: 18px;
     color:#555;
 }

 .newUserSelect{
     height: 40px;
     border-radius: 5px;
 }

 .newUserButton{
     width: 200px;
     border: none;
     background-color:darkblue;
     color: #fff;
     padding: 7px 10px;
     font-weight: 600;
     border-radius: 10px;
     margin-top: 30px;
     cursor: pointer;
     text-align: center;
     
 }