.featured{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.featuredItem{
    flex:1;
    margin:5px 20px;
    padding:30px;
    border-radius:10px;
    cursor: pointer;
    -webkit-box-shadow: 1px 1px 15px 2px rgba(0,0,0,0.65); 
    box-shadow: 1px 1px 7px -4px rgba(0,0,0,0.65);
}

.featuredTitle{
    font-size: 20px;
}

.featuredMoneyContainer{
    margin: 10px 0px;
    display: flex;
    align-items: center;
}

.featuredMoney{
    font-size: 30px;
    font-weight: 600;
}

.featuredRate{
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.featuredIcon{
    font-size: 14px;
    margin-left: 5px;
    color:green;
}
.featuredIcon.negative{
    color:red;
}

.featuredSub{
    font-size: 15px;
    color:gray;
}