.productList{
    flex:4;
}

.productListItem{
    display: flex;
    align-items: center;
}
.productListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
    object-fit: cover;
}

.productListEdit{
    border:none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color:#3bb072;
    color: #fff;
    cursor: pointer;
    margin-right: 20px;
}

.productListEditDelete{
    color: red;
    cursor: pointer;
}