.userList{
    flex:4;
}
.userListUser{
    display: flex;
    align-items: center;
}
.userListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 10px;
    object-fit: cover;
}

.userListEdit{
    border:none;
    border-radius: 10px;
    padding: 5px 10px;
    background-color:#3bb072;
    color: #fff;
    cursor: pointer;
    margin-right: 20px;
}

.userListEditDelete{
    color: red;
    cursor: pointer;
}