.user{
    flex:4;
    padding:20px;
}

.userTitleContainer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.userTop{
    display: flex;
    margin-bottom: 20px;
}
.userAddButton{
    width: 80px;
    border:none;
    padding:5px;
    cursor:pointer;
    border-radius: 5px;
    background-color:teal;
    color:white;
    font-size: 16px;
}

.userContainer{
    display: flex;
    margin-top: 20px;
}
.userShow{
    flex: 1;
    padding: 20px;
    -webkit-box-shadow: 1px 1px 15px 2px rgba(0,0,0,0.65); 
    box-shadow: 1px 1px 7px -4px rgba(0,0,0,0.65);

}

.userUpdate{
    flex: 2;
    padding: 20px;
    -webkit-box-shadow: 1px 1px 15px 2px rgba(0,0,0,0.65); 
    box-shadow: 1px 1px 7px -4px rgba(0,0,0,0.65);
    margin-left: 20px;
}

.userShowTop{
    display: flex;
    align-items: center;
}

.userShowImg{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.userShowTopTitle{
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}

.userShowUsername{
    font-weight: 600;
}

.userShowUserTitle{
    font-weight: 300;
}

.userShowTitle{
    
    font-size: 14px;
    font-weight: 600;
    color:rgb(175, 167, 167);
}

.userShowBottom{
    margin-top: 20px;
}

.userShowInfo{
    display: flex;
    align-items: center;
    margin: 20px 0px;
    color: #444;
}

.userShowIcon{
    font-size: 16px !important;
}

.userShowInfoTitle{
    margin-left: 10px;
}
.userUpdateImg{
    width: 100px;
    height: 100px;
    border-radius: 10px;
    object-fit: cover;
    margin-left: 20px;
}

.userUpdateTitle{
    font-size: 24px;
    font-weight: 600;
}

.userUpdateForm{
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

.userUpdateItem{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}

.userUpdateItem>label{
    margin-bottom: 5px;
    font-size: 14px;
}

.userUpdateInput{
    width: 250px;
    border:none ;
    border-bottom: 1px solid gray;
    height: 30px;
}

.userUpdateRight{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.userUpdateUpload{
     display: flex;
     align-items: center;
}
.userUpdateIcon{
    cursor: pointer;
}

.userUpdateButton{
    border-radius: 5px;
    border:none ;
    padding: 5px;
    cursor: pointer;
    background-color: darkblue;
  color:white;
    font-weight: 600;
}